@import url('./__close-button/avatar-popup__close-button.css');
@import url('./__popup-container/avatar__popup-container.css');

.avatar-popup {
  display: flex;
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s ease-in-out;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, .5);
}
